/** AcaNotes vars **/
/** font sizing **/
/** Screen break points **/
.container {
  width: calc(100% - 40px);
  margin-left: 20px;
  margin-top: 60px;
}
@media screen and (min-width: 576px) {
  .container {
    width: calc(100% - 40px);
    margin-left: 20px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 80%;
    margin-left: 10%;
  }
}
@media screen and (min-width: 992px) {
  .container {
    width: 75%;
    margin-left: 12.5%;
  }
}
@media screen and (min-width: 2140px) {
  .container {
    max-width: 1361px;
  }
}
.vertical-align {
  position: absolute;
  top: 50%;
  margin: 0px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.card-1 {
  margin-bottom: 24px;
  margin-top: 24px;
  border-radius: 5px;
  padding: 25px;
  width: 600px;
  box-shadow: 0 2px 16px rgba(46, 83, 81, 0.18);
  background-color: #fefefe;
  width: 100%;
}
.UserCard {
  position: relative;
  display: inline-block;
  max-width: 240px;
  margin-top: 24px;
  margin-bottom: 24px;
  margin-right: 24px;
  margin-left: 24px;
  text-align: center;
  font-size: 1.25rem;
  color: inherit;
  background-color: #fefefe;
  box-shadow: 0 2px 16px rgba(46, 83, 81, 0.18);
  padding: 12px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  border-radius: 4px;
}
.UserCard * {
  cursor: pointer !important;
}
.UserCard .user-name {
  margin-top: 12px;
}
.UserCard:hover {
  color: inherit;
  box-shadow: 0 2px 24px rgba(46, 83, 81, 0.24);
  -webkit-transform: translateY(-12px);
          transform: translateY(-12px);
}
