/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/** AcaNotes vars **/
/** font sizing **/
/** Screen break points **/
.container {
  width: calc(100% - 40px);
  margin-left: 20px;
  margin-top: 60px;
}
@media screen and (min-width: 576px) {
  .container {
    width: calc(100% - 40px);
    margin-left: 20px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 80%;
    margin-left: 10%;
  }
}
@media screen and (min-width: 992px) {
  .container {
    width: 75%;
    margin-left: 12.5%;
  }
}
@media screen and (min-width: 2140px) {
  .container {
    max-width: 1361px;
  }
}
.vertical-align {
  position: absolute;
  top: 50%;
  margin: 0px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.card-1 {
  margin-bottom: 24px;
  margin-top: 24px;
  border-radius: 5px;
  padding: 25px;
  width: 600px;
  box-shadow: 0 2px 16px rgba(46, 83, 81, 0.18);
  background-color: #fefefe;
  width: 100%;
}
html {
  font-size: 14px;
}
@media only screen and (max-width: 992px) {
  html {
    font-size: 14px;
  }
}
@media only screen and (max-width: 576px) {
  html {
    font-size: 12px;
  }
}
@font-face {
  font-family: 'Cantarell';
  src: url(./static/media/Cantarell-Regular.ea5ec473.ttf);
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: 'Open Sans', Arial, sans-serif;
  color: #33383a;
  background-color: #fefefe;
  line-height: 1.5;
  font-weight: 400;
}
h1 {
  font-family: 'Cantarell', 'Open Sans', Arial, sans-serif;
  font-size: 5rem;
  font-weight: 900;
  margin-top: 36px;
  margin-bottom: 36px;
  font-weight: 600;
}
h2 {
  font-family: 'Cantarell', 'Open Sans', Arial, sans-serif;
  font-size: 3rem;
  font-weight: 900;
  margin-top: 24px;
  margin-bottom: 24px;
  font-weight: 600;
}
h3 {
  font-size: 2rem;
  margin-top: 24px;
  margin-bottom: 24px;
  font-weight: 400;
}
h4 {
  font-size: 1.5rem;
  margin-top: 24px;
  margin-bottom: 24px;
  font-weight: 400;
}
p {
  font-size: 1.25rem;
  margin-top: 12px;
  margin-bottom: 12px;
  font-weight: 300;
}
