/** AcaNotes vars **/
/** font sizing **/
/** Screen break points **/
.container {
  width: calc(100% - 40px);
  margin-left: 20px;
  margin-top: 60px;
}
@media screen and (min-width: 576px) {
  .container {
    width: calc(100% - 40px);
    margin-left: 20px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 80%;
    margin-left: 10%;
  }
}
@media screen and (min-width: 992px) {
  .container {
    width: 75%;
    margin-left: 12.5%;
  }
}
@media screen and (min-width: 2140px) {
  .container {
    max-width: 1361px;
  }
}
.vertical-align {
  position: absolute;
  top: 50%;
  margin: 0px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.card-1 {
  margin-bottom: 24px;
  margin-top: 24px;
  border-radius: 5px;
  padding: 25px;
  width: 600px;
  box-shadow: 0 2px 16px rgba(46, 83, 81, 0.18);
  background-color: #fefefe;
  width: 100%;
}
.Navbar .login-item {
  float: right;
}
.Navbar .login-item > .login-icon {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.Navbar .login-item:hover > .login-icon {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
.Navbar .register-item:hover > .register-icon {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
.Navbar .register-item {
  float: right;
}
.Navbar .register-item > .register-icon {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.Navbar .profile-item {
  float: right;
  font-size: 1rem;
}
.Navbar .profile-item > .profile-icon {
  font-size: 1.25rem;
}
.Navbar .logout-item {
  float: right;
}
.Navbar .logout-item > .logout-icon {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
