/** AcaNotes vars **/
/** font sizing **/
/** Screen break points **/
.container {
  width: calc(100% - 40px);
  margin-left: 20px;
  margin-top: 60px;
}
@media screen and (min-width: 576px) {
  .container {
    width: calc(100% - 40px);
    margin-left: 20px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 80%;
    margin-left: 10%;
  }
}
@media screen and (min-width: 992px) {
  .container {
    width: 75%;
    margin-left: 12.5%;
  }
}
@media screen and (min-width: 2140px) {
  .container {
    max-width: 1361px;
  }
}
.vertical-align {
  position: absolute;
  top: 50%;
  margin: 0px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
